@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&family=Shrikhand&display=swap");

// MIXINS
@function strip-unit($number) {
    @if type-of($number) == "number" and not unitless($number) {
        @return $number / ($number * 0 + 1);
    }

    @return $number;
}

@mixin fluid-text($min-font-size, $max-font-size, $line-height, $break-start, $break-end) {
    /////
    // Calculations font size
    /////
    $fsm: (strip-unit($max-font-size) - strip-unit($min-font-size)) /
        (strip-unit($break-end) - strip-unit($break-start));
    $fsb: #{$min-font-size - $fsm * $break-start};
    $fsmx: #{$fsm * 100vw};
    /////
    // Output
    /////
    font-size: #{$min-font-size};
    line-height: #{$line-height};

    @media (min-width: #{$break-start}) {
        font-size: calc(#{$fsmx} + #{$fsb});
    }

    @media (min-width: $break-end) {
        font-size: #{$max-font-size};
    }
}

// COLORS
$violet: #9356dc;
$rose: #ff79da;
$vert: #99e2d0;
$blanc: #fff;
$gris: #383838;
$gris-light: #f0f0f0;

.blanc {
    color: $blanc;
}

html {
    font-family: "Roboto", sans-serif;
}

body {
    margin: 0;
    padding: 0;
}

.title {
    font-family: "Shrikhand", cursive;
}

.h1 {
    @include fluid-text(25px, 30px, 1.1, 320px, 475px);
    font-weight: 900;
}
.h4 {
    @include fluid-text(15px, 18px, 1.1, 320px, 475px);
    font-weight: 300;
}

section {
    padding: 30px 20px;
}

// NAV
.nav {
    width: 100%;
    padding: 15px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    &-logo {
    }
}

// HEADER
.header {
    text-align: center;
}
.location {
    font-size: 16px;
    font-weight: 500;
    background-color: #e6e6e6;
    color: $gris;
    padding: 10px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    &::before {
        content: url("../images/location.svg");
        margin-right: 10px;
    }
}
.intro {
    background-color: $gris-light;
    padding: 30px 30px;
    & .button {
        color: $blanc;
        font-weight: 500;
        font-size: 12px;
        // width: 180px;
        // height: 40px;
        padding: 15px 20px;
        border-radius: 50px;
        border: none;
        background: linear-gradient(0deg, $violet 30%, $rose);
        box-shadow: #a5a5a5 0 4px 8px;
    }
}

// FONCTIONNEMENT
.fonctionnement {
    &-options {
        height: 50px;
        width: calc(100% - 10px);
        margin-left: 10px;
        margin-bottom: 20px;
        border-radius: 15px;
        background-color: $gris-light;
        box-shadow: #c4c4c4 0 4px 10px;
        display: flex;
        align-items: center;
        list-style-type: none;
        counter-reset: css-counter 0; /* initializes counter to 0; use -1 for zero-based numbering */
        p {
            font-size: 13px;
            font-weight: bold;
            list-style-type: none;
            counter-reset: css-counter 0; /* initializes counter to 0; use -1 for zero-based numbering */
        }
        &::before {
            counter-increment: css-counter 1; /* Increase the counter by 1. */
            content: counter(css-counter) " "; /* Apply counter before children's content. */
        }
    }
}

