// RESTAURANTS
.restaurants {
    background-color: $gris-light;

    &-visuel {
        & img {
            width: 100%;
            height: 200px;
            object-fit: cover;
            object-position: center center;
            border-radius: 20px 20px 0 0;
        }
        &-description {
            box-shadow: 0 0 15px rgb(194, 194, 194);
            background-color: white;
            border-radius:0 0 20px 20px;
            padding: 10px;
            position: relative;
            font-size: 13px;
            p {
                margin-bottom: 5px;
            }
        }
    }
}
